import { number, withKnobs } from '@storybook/addon-knobs';
import { moduleMetadata, storiesOf } from '@storybook/angular';
import { BreadcrumbItem } from 'carbon-components-angular';
import { itemsWithTitle } from './page-header.component';
import { SterlingPageHeaderModule } from './page-header.module';

const createBreadcrumbItems = (count: number, content = 'Breadcrumb'): BreadcrumbItem[] =>
	// fill(0) so we have something useful to map over
	Array(count).fill(0).map((x, i) => ({
		content: `${content} ${i + 1}`,
		href: '#' + (i + 1)
	}));

storiesOf('Sterling | Page header', module).addParameters({ storySource: { source: __STORY__, locationsMap: __ADDS_MAP__ } }).addDecorator(withSourceLoader(__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__))
	.addDecorator(moduleMetadata({
		imports: [ SterlingPageHeaderModule ]
	}))
	.addDecorator(withKnobs)
	.add('Basic', () => ({
		template: `
			<sc-page-header [items]="items()"></sc-page-header>
		`,
		props: {
			numberOfItems: number('numberOfItems', 3),
			items() {
				return itemsWithTitle(createBreadcrumbItems(this.numberOfItems), 'Hello world');
			}
		}
	}));
